import { Store } from '../../service/store.se'
import { MetadataKeys } from '../../utils/types'
import { AnyZodObject } from 'zod'

/**
 * Validation middleware. A function which is called before the route handler.
 *
 * @param schema schema object.
 * @returns
 */
export function Validation(schema: AnyZodObject): MethodDecorator {
    return (Target, propertyKey) => {
        // Define a new metadata object and set it up in the container Store.
        Store.container.define<AnyZodObject>(Target.constructor.prototype, schema, MetadataKeys.__api_method_validation__, propertyKey)
    }
}
